.add-ons__items{display:flex;flex-direction:column;justify-content:space-between;gap:1rem}@media screen and (max-width: 425px){.add-ons__items{margin:1rem 0}}.add-ons__items .add-ons__item{display:flex;flex-direction:row;justify-content:space-between;padding:1rem;border:1px solid var(--light-gray);border-radius:10px}.add-ons__items .add-ons__item .item__info{display:flex;flex-direction:row;justify-content:center;align-items:center}.add-ons__items .add-ons__item .item__info input[type=checkbox]{position:relative;margin-right:1rem;width:20px;height:20px;-webkit-appearance:none;background-color:var(--white);border:1px solid var(--light-gray);box-shadow:inset 0 0 5px rgba(255,255,255,.2)}.add-ons__items .add-ons__item .item__info input[type=checkbox]:hover{cursor:grab}.add-ons__items .add-ons__item .item__info input:checked[type=checkbox]{background-color:var(--purplish-blue)}.add-ons__items .add-ons__item .item__info input:checked[type=checkbox]:after{font-weight:700;content:"✔";color:var(--white);margin-left:4px}.add-ons__items .add-ons__item .item__info h1{font-size:1rem;margin-bottom:.5rem}@media screen and (max-width: 425px){.add-ons__items .add-ons__item .item__info h1{margin-bottom:.3rem}}.add-ons__items .add-ons__item .item__info p{color:var(--cool-gray)}@media screen and (max-width: 425px){.add-ons__items .add-ons__item .item__info p{font-size:.8rem}}.add-ons__items .add-ons__item .item__price{display:flex;justify-content:center;align-items:center}.add-ons__items .add-ons__item .item__price p{font-size:.8rem;color:var(--purplish-blue)}.add-ons__items .add-ons__item:hover{border:1px solid var(--purplish-blue);cursor:pointer}.add-ons__items .checked{background-color:var(--magnolia)}